import { ChevronDown, MessageSquare, Send, Copy, ThumbsUp, PlusCircle, AlertTriangle } from 'lucide-react';

export default function AiCopilotPanel({
  onClose,
  aiQuery,
  setAiQuery,
  onAskAiCopilot,
  copilotThinking,
  aiSuggestion,
  hasInternalContent,
  onInsertAiSuggestion,
  timeSaved
}) {
  return (
    <div className="w-1/4 border-l border-gray-200 bg-white overflow-y-auto flex flex-col">
      <div className="p-4 border-b border-gray-200 bg-blue-50">
        <div className="flex justify-between items-center">
          <h3 className="font-semibold text-blue-700">AI Copilot</h3>
          <button 
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <ChevronDown className="h-5 w-5" />
          </button>
        </div>
        <p className="text-xs text-blue-600 mt-1">Ask me anything about this conversation</p>
      </div>
      
      <div className="p-4 flex-1 overflow-y-auto">
        <div className="flex mb-4">
          <div className="w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center mr-2">
            <MessageSquare className="h-4 w-4 text-blue-600" />
          </div>
          <div className="flex-1">
            <p className="text-xs font-medium text-blue-700">AI Copilot</p>
            <p className="text-sm mt-1">
              How can I help with this conversation? Ask me for suggestions or recommendations.
            </p>
          </div>
        </div>
        
        <div className="mb-4">
          <div className="flex mb-2">
            <input
              type="text"
              value={aiQuery}
              onChange={(e) => setAiQuery(e.target.value)}
              placeholder="Ask something like 'How should I respond?'"
              className="flex-1 p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
            />
            <button
              onClick={onAskAiCopilot}
              disabled={!aiQuery.trim() || copilotThinking}
              className={`ml-2 px-3 py-2 rounded-md ${
                !aiQuery.trim() || copilotThinking
                  ? 'bg-gray-200 text-gray-500 cursor-not-allowed'
                  : 'bg-blue-600 text-white hover:bg-blue-700'
              }`}
            >
              {copilotThinking ? (
                <span className="flex items-center">
                  <div className="animate-spin h-4 w-4 border-2 border-white border-t-transparent rounded-full mr-1"></div>
                  Thinking
                </span>
              ) : (
                <Send className="h-4 w-4" />
              )}
            </button>
          </div>
          
          <div className="text-xs text-gray-500 flex justify-between">
            <button className="hover:text-blue-600">What's our refund policy?</button>
            <button className="hover:text-blue-600">How should I handle this?</button>
          </div>
        </div>
        
        {aiSuggestion && (
          <div className="p-4 bg-gray-50 rounded-lg">
            <div className="flex justify-between items-start mb-2">
              <h4 className="text-sm font-medium text-gray-700">Suggested Response</h4>
              <div className="flex">
                <button 
                  onClick={onInsertAiSuggestion}
                  className="text-blue-600 hover:text-blue-800 mr-2"
                  title="Insert into reply"
                >
                  <Copy className="h-4 w-4" />
                </button>
                <button 
                  className="text-gray-500 hover:text-gray-700"
                  title="Thumbs up"
                >
                  <ThumbsUp className="h-4 w-4" />
                </button>
              </div>
            </div>
            
            {hasInternalContent && (
              <div className="mb-3 flex items-center bg-amber-50 p-2 rounded text-xs text-amber-800">
                <AlertTriangle className="h-3 w-3 mr-1" />
                Contains internal knowledge. Review before sending.
              </div>
            )}
            
            <p className="text-sm whitespace-pre-line">{aiSuggestion}</p>
            
            <div className="mt-3 flex justify-end">
              <button
                onClick={onInsertAiSuggestion}
                className="px-3 py-1.5 bg-blue-100 text-blue-700 rounded-md text-sm hover:bg-blue-200 transition-colors flex items-center"
              >
                <PlusCircle className="h-3 w-3 mr-1" />
                Insert into Reply
              </button>
            </div>
          </div>
        )}
      </div>
      
      {/* Efficiency metrics */}
      <div className="p-4 border-t border-gray-200 bg-blue-50">
        <h4 className="text-xs font-medium text-blue-700">Efficiency Metrics</h4>
        <div className="flex justify-between mt-2">
          <div>
            <p className="text-xs text-gray-500">Time Saved</p>
            <p className="text-sm font-medium">{timeSaved} minutes</p>
          </div>
          <div>
            <p className="text-xs text-gray-500">Efficiency Boost</p>
            <p className="text-sm font-medium">31%</p>
          </div>
        </div>
      </div>
    </div>
  );
} 